import Vue from 'vue';
import App from './App.vue';
//импортируем роутер к корневому экземпляру вью
import router from './router/index';
import store from './store/index';

Vue.config.productionTip = false;

new Vue({
  //в объекте инициализации передаем экземпляр
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
