<!-- eslint-disable max-len -->
<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="decrementButton">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <input :value="productsAmount" @input="$emit('update:productsAmount', event.target.value)" />
    <button type="button" aria-label="Добавить один товар" @click.prevent="incrementButton">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>

export default {
  props: {
    productsAmount: {
      type: Number,
      required: true,
    },
  },
  methods: {
    incrementButton() {
      this.$emit('update:productsAmount', this.productsAmount + 1);
    },
    decrementButton() {
      if (this.productsAmount > 1) {
        this.$emit('update:productsAmount', this.productsAmount - 1);
      }
    },
  },
};
</script>
